import React from 'react';
import {  Outlet , Link } from 'react-router-dom';
import myStyle from './MainBlog.module.scss';
import NavDir from './NavDir'; 



function MainBlog( {initialNodes})  {
  return (
    <div className={myStyle.container }>
      <div className={myStyle.header }>
      <div className={myStyle.tabs}>
        {/*  ,   */}
        <Link to="/myBlog/mkTest" className={myStyle.tab}>mk test (X)</Link>
        <Link to="/myBlog/memoChart" className={myStyle.tab}>memoChart</Link>
        {/* <Link to="myBlog/memoChart" className={myStyle.tab}>Test1</Link>
        <Link to="myBlog/memoChart" className={myStyle.tab}>Test1</Link>
        <Link to="myBlog/memoChart" className={myStyle.tab}>Test1</Link>
        <Link to="myBlog/memoChart" className={myStyle.tab}>Test1</Link>
        <Link to="myBlog/memoChart" className={myStyle.tab}>Test1</Link>
        <Link to="myBlog/memoChart" className={myStyle.tab}>Test1</Link>
        <Link to="myBlog/memoChart" className={myStyle.tab}>Test1</Link> */}
        {/*   */}
      </div>
      </div>
      <div className={myStyle.content }>
        <div className={myStyle.content1}>
          <Outlet/> 
        </div>
        <div className={myStyle.content2}>
          <NavDir fileData={initialNodes}/>
        </div>
      </div>

    </div>
  );
}

export default MainBlog;
