function TestView({ testDatas , testDatas1 ,testDatas2 }) {
  return (
    <div>
      <hr></hr>
      <div>
          {testDatas ? // JSON  
          (<div>{JSON.stringify(testDatas)}</div> ) : 
          (<p>Loading...</p> )}
        </div>
        <hr></hr>
        <div>
          {testDatas1.map(subData => (
            <div key={subData.id}>
                <h1>{subData.title}</h1>
                <p>count: {subData.count}</p>
                <p>float: {subData.float}</p>
                <p>url: {subData.url}</p>
                <p>color: {subData.color.join(" , ")}</p>
              {/*    */}
            </div>
          ))}
        </div>
        <hr></hr>
        <div>
          {testDatas2.map((subData) => (
            <div key={subData.id}>
                <h1>{subData.title}</h1>
                <p>count: {subData.count}</p>
                <p>float: {subData.float}</p>
                <p>url: {subData.url}</p>
                <p>color: {subData.color.join(" , ")}</p>
              {/*    */}
            </div>
          ))}
        </div>
        <hr></hr>
    </div>
  );
}


